@font-face {
  font-family: "AAHA";
  src: url(fonts/AAHA-Regular.woff2) format(woff2);
}
html, body {
  font-size: 18px;
  font-family: "freight-text-pro", serif;
}

a:link,
a:visited {
  color: #c48726;
}
a:link:hover,
a:visited:hover {
  text-decoration: none;
  color: #383131;
}

h1 {
  font-family: "AAHA";
  font-size: 2.2rem;
  line-height: 2rem;
  white-space: nowrap;
  position: relative;
  font-weight: normal;
  padding: 0;
  margin: 0;
}

h2 {
  text-align: left;
  padding: 0;
  margin: 0;
  font-weight: normal;
  font-size: 1.8rem;
  line-height: 2rem;
}

#header h3 {
  text-align: left;
}

.menuButton {
  border: none;
  background: transparent;
  font: inherit;
  padding: 0;
  margin: 0;
  transition: text-indent 0.2s;
  cursor: pointer;
}
.menuButton.active, .menuButton:hover {
  text-indent: 1em;
}
.menuButton.active {
  font-weight: bold;
}

h3, h4, h5 {
  text-align: center;
}

h1, h3, h5 {
  color: #c48726;
}

ul {
  padding: 0;
}
ul li {
  list-style-type: circle;
}

ol {
  padding: 0;
}
ol li::marker {
  font-weight: bold;
}

li {
  margin-bottom: 0.5em;
}
li::marker {
  color: #c48726;
}

a.tool_tip {
  color: inherit;
  text-decoration: underline;
}

#tool_tip {
  max-width: 300px;
  padding: 0.5em 0.5em 0.5em 1em;
  border-top-left-radius: 1em;
  border-top-right-radius: 1em;
  border-bottom-right-radius: 1em;
  border: 1px solid #c48726;
  font-size: 0.75em;
  background: #383131;
  color: #FFFFFF;
  position: fixed;
  z-index: 9999;
  opacity: 0;
  top: -300px;
  left: -300px;
  transform: translateY(calc(-100% - 1rem));
  transition: opacity 0.2s, top 0.2s, left 0.2s;
}
#tool_tip.active {
  opacity: 1;
}

#container {
  /* text-align:center; */
  display: grid;
  width: 50vw;
  margin-left: calc(25vw + 1em);
  grid-column-gap: 0;
}

.image {
  height: 0;
  position: relative;
  left: 50vw;
  top: 1em;
  width: calc(15vw - 3em);
}

#header {
  width: 25%;
  position: fixed;
  padding: 1rem;
}
#header ul li {
  list-style-type: none;
}

img {
  max-width: 100%;
}

#left h1 {
  transform: translateX(-75%);
  left: 400px;
}

#center h1 {
  transform: translateX(-75%);
}

#right h1 {
  transform: translateX(-75%);
  left: -400px;
}

#footer {
  background: #383131;
  padding: 2rem;
}

html, body {
  margin: 0;
  padding: 0;
  color: #383131;
  background-color: #FFFFFF;
}

.interior {
  padding: 0 1em;
}

